.page-container {
  scroll-snap-type: y mandatory;
  background-color: #000;
  height: 100vh;
  width: 100vw;
  overflow-y: scroll;
  margin: 0; 
  padding: 0; 
}

.intro-section {
  height: 100vh;
  width: 100vw;
  background-color: var(--accent-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  scroll-snap-align: start;
  position: relative;
  margin: 0; 
  padding: 0; 
  overflow: hidden; 
}



.home-container {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  scroll-snap-align: start;
  padding: 0; 
  margin: 0; 
  overflow: hidden; 
}


.cta-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}


.contact-button {
  background-color: var(--accent-color, #ffeb3b); /* fallback to yellow */
  color: #000;
  font-family: 'Lucida Console', Monaco, monospace;
  font-size: 2rem;
  font-weight: 600;
  border: 3px solid #000;
  border-radius: 100px;
  padding: 1.2rem 2.5rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 160px;
  min-height: 50px;
  text-align: center;
  transition: background 0.2s, color 0.2s, transform 0.2s;
  box-shadow: none;
  gap: 0; 
  text-decoration: none;
}

.contact-button:hover {
  transform: translateY(-2px);
}

.info-section {
  height: 100vh;
  width: 100vw;
  background: #000;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  scroll-snap-align: start;
  padding: 0 2rem;
  box-sizing: border-box;
  text-align: center;
}

.info-question {
  color: var(--accent-color);
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 2rem;
  letter-spacing: 0.5px;
}

.info-answer {
  color: #fff;
  font-size: 1.5rem;
  max-width: 800px;
  line-height: 1.7;
  font-weight: 500;
}

.logo-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.footer-section {
  height: 100vh;
  width: 100vw;
  background: #000;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: stretch;
  scroll-snap-align: start;
  margin: 0;
  padding: 0;
  position: relative;
}

.footer-main {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 8vw;
}

.footer-col {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.footer-col-left {
  align-items: center;
  justify-content: center;
}

.footer-col-right {
  align-items: center;
  justify-content: center;
}

.footer-logo {
  height: 250px;
  width: auto;
}

.footer-link {
  color: var(--accent-color);
  text-decoration: none;
  font-size: 2rem;  
  font-weight: 700;        
  transition: color 0.3s;
  padding: 1rem 2.5rem;    
  border-radius: 40px;
  display: inline-block;
  text-align: center;
}

.footer-link:hover {
  transform: translateY(-2px);
}


.footer-col-right .contact-button {
  font-size: 2rem;         
  padding: 1.2rem 3rem;    
  margin-top: 1rem;
}

.footer-copy {
  width: 100%;
  text-align: center;
  padding: 2rem 0 5rem 0;
  color: #fff;
  font-family: 'Lucida Console', Monaco, monospace;
  font-size: 1rem;
  letter-spacing: 0.5px;
  opacity: 0.8;
}



@media (max-width: 700px) {
  .page-container,
  .intro-section,
  .home-container,
  .footer-section {
    width: 100vw;
    min-height: 100vh;
    height: auto;
    padding: 0;
    box-sizing: border-box;
  }

  .info-section {
    width: 100vw;
    min-height: 50vh;
    height: auto;
    padding: 0;
    box-sizing: border-box;
  }

  .intro-section,
  .home-container,
  .info-section {
    padding: 0 4vw;
  }

  .logo-container img,
  .footer-logo {
    max-width: 80vw;
    height: auto;
    max-height: 30vh;
  }

  .info-question {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    text-align: left;
  }

  .info-answer {
    font-size: 1rem;
    max-width: 95vw;
    text-align: left;
  }

  .contact-button,
  .footer-link {
    font-size: 1.1rem !important;
    padding: 1rem 1.5rem !important;
    width: 90vw;
    max-width: 320px;
    margin: 1rem auto 0 auto;
    display: block;
    text-align: center;
  }

  .footer-section {
    padding-bottom: 0;
    min-height: 100vh;
    height: auto;
  }

  .footer-main {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem 0 0 0;
    gap: 2rem;
  }

  .footer-col {
    width: 100%;
    align-items: center;
    justify-content: center;
    margin-bottom: 0;
  }

  .footer-copy {
    font-size: 0.9rem;
    padding: 1rem 0 2rem 0;
  }
}

/* 2. Header and nav responsive */
@media (max-width: 700px) {
  header {
    height: auto;
    flex-direction: column;
    align-items: flex-start;
    padding: 0.5rem 0;
  }

  .header-logo {
    font-size: 1.1rem;
    margin-left: 1rem;
    margin-bottom: 0.5rem;
  }

  nav {
    width: 100%;
    justify-content: flex-start;
    gap: 1.2rem;
    padding: 0 1rem;
  }

  nav a, .App-link {
    font-size: 1rem;
    padding: 0.3rem 0.5rem;
  }
}

/* 3. Contact page responsive */
@media (max-width: 700px) {
  .contact-container {
    min-height: 100vh;
    padding: 0 2vw;
  }
  .contact-header h1 {
    font-size: 1.4rem;
  }
  .contact-form {
    max-width: 98vw;
    padding: 1rem;
  }
  .form-input,
  .form-textarea {
    font-size: 1rem;
    padding: 0.5rem;
  }
  .submit-button {
    font-size: 1rem;
    padding: 0.8rem;
  }
}

