:root {
  --main-font: "Lucida Console", "Courier New", monospace;
  --bg-color: #000000;
  --accent-color: #ffeb3b;
  --header-bg: #20232a;
  --header-color: #fff;
}

body {
  margin: 0;
  font-family: var(--main-font);
  background: var(--main-bg);
  color: var(--main-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 5vh;
  background: var(--bg-color);
  color: #fff;
  z-index: 1000;
  display: flex;
  align-items: center;
}

.header-logo {
  color: var(--accent-color);
  font-weight: bold;
  font-size: 1.3rem;
  text-decoration: none;
  margin-left: 2rem;
  margin-right: auto;
  letter-spacing: 1px;
  transition: opacity 0.2s;
  line-height: 1;
  display: flex;
  align-items: center;
}

.header-logo:hover {
  opacity: 0.7;
}


nav {
  display: flex;
  justify-content: flex-end;
  gap: 2rem;
  padding: 0 2rem;
  color: #fff;
}

nav a,
.App-link {
  color: #fff !important;
  text-decoration: none;
  font-weight: 600;
  transition: color 0.2s, transform 0.3s;
}

nav a:hover {
  transform: translateY(-2px);
}


* {
  scrollbar-width: none;           
  -ms-overflow-style: none;        
}

*::-webkit-scrollbar {
  display: none;                  
}

main {
  padding-top: 5vh; 
}




