.contact-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center; 
  align-items: center;    
  background: #000;
  color: #fff;

}


.contact-content {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center; 
}

.contact-form {
  width: 100%;
  max-width: 600px;
  background-color: #010100;
  border-radius: 50px;
  border: 2px solid var(--accent-color);
  padding: 2rem;
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
  display: flex;
  flex-direction: column;
  align-items: center; 
}

@media (max-width: 700px) {
  .contact-content {
    padding: 0;
  }

  .contact-form {
    max-width: 100vw;
    width: 100vw;
    padding: 0.5rem 0;
    margin: 0;
    border-radius: 0;
    box-sizing: border-box;
    border-width: 0 0 2px 0; /* Optional: only bottom border for mobile look */
    min-height: 60vh;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .contact-form iframe {
    width: 98vw !important;
    min-width: 0;
    max-width: 100vw;
    height: 80vh;
    min-height: 400px;
    border-radius: 12px;
    border: none;
    box-sizing: border-box;
    display: block;
    margin: 0 auto;
    background: #fff;
  }
}


